import React from "react";
import { useLocation } from "react-router-dom";
import { Paths } from "../../../constants";
import { BreadcrumbLink, BreadcrumbProps } from "../common/Breadcrumb";

export function useInfoBreadCrumb(): BreadcrumbProps {
  const location = useLocation();
  const player = React.useMemo(
    () => decodeURIComponent(location.pathname.split("/")[2]),
    [location.pathname]
  );
  const linkList: Array<BreadcrumbLink> = [
    {
      name: "プレイヤー一覧",
      href: Paths.Top,
    },
    {
      name: player,
      href: player,
    },
  ];
  return { linkList };
}
