import React from "react";
import { useLocation } from "react-router-dom";
import { imageSrc } from "../../../service/imageSrc";
import { snsLink } from "../../../service/snsLink";
import { historys } from "../../../service/takadamuraHistoryService";
import { InfoCardProps } from "./InfoCard";

export function useInfoCard(): InfoCardProps {
  const location = useLocation();
  const player = React.useMemo(
    () => decodeURIComponent(location.pathname.split("/")[2]),
    [location.pathname]
  );
  const links = React.useMemo(() => snsLink(player), [player]);
  const onClickYoutube = React.useCallback(
    () => window.open(links.youtube, "_blank"),
    [links.youtube]
  );
  const onClickTwitter = React.useCallback(
    () => window.open(links.twitter, "_blank"),
    [links.twitter]
  );
  const src = React.useMemo(() => imageSrc(player) || "", [player]);
  const firstDate = React.useMemo(() => {
    const first = [...historys].find((h) =>
      Boolean(h.players.find((p) => p === player))
    );
    return first?.timestamp || "";
  }, [player]);
  const lastDate = React.useMemo(() => {
    const last = [...historys]
      .reverse()
      .find((h) => Boolean(h.players.find((p) => p === player)));
    return last?.timestamp || "";
  }, [player]);
  return {
    alt: player,
    src,
    player,
    onClickTwitter: links.twitter ? onClickTwitter : undefined,
    onClickYoutube: links.youtube ? onClickYoutube : undefined,
    firstDate,
    lastDate,
  };
}
