import data from "../data.json";
import { TakadamuraHistory } from "../domain/takadamuraHistory";

export const historys = data as Array<TakadamuraHistory>;
export const players = listPlayers();
export const participationMapping = participationMap();

function listPlayers(): Array<string> {
  const players = historys.map((h) => h.players).flat();
  return Array.from(new Set(players));
}

function participationMap() {
  return players
    .map((player) => ({
      player,
      count: historys
        .map((h) => h.players)
        .flat()
        .filter((p) => p === player).length,
    }))
    .sort((x, y) => {
      if (x.count > y.count) {
        return -1;
      } else if (x.count < y.count) {
        return 1;
      }
      return 0;
    });
}
