import { Box, Stack, Theme, useMediaQuery } from "@mui/material";
import { grey } from "@mui/material/colors";
import { Header } from "../organisms/common/Header";

export type MainTemplateProps = {
  info: JSX.Element;
  graph: JSX.Element;
  breadcrumb: JSX.Element;
};

const DRAWER_WIDTH = 240;

export function InfoTemplate(props: MainTemplateProps) {
  const pc = useMediaQuery<Theme>((theme) => theme.breakpoints.up("sm"));
  return (
    <Stack
      direction="column"
      height={pc ? "100vh" : "100%"}
      sx={(theme) => ({
        backgroundColor: theme.palette.mode === "light" ? grey[100] : grey[900],
      })}
    >
      <Box width="100vw">
        <Header drawerWidth={DRAWER_WIDTH} />
      </Box>
      <Box
        sx={{
          direction: pc ? "row" : "column",
          width: pc ? `calc(100vw - ${DRAWER_WIDTH + 33}px)` : "100vw",
        }}
        mt={pc ? 10 : 8}
        ml={pc ? `${DRAWER_WIDTH + 16}px` : 0}
      >
        <Box ml={pc ? 0 : 2}>{props.breadcrumb}</Box>
        <Stack
          height="100%"
          mt={1}
          direction={pc ? "row" : "column"}
          spacing={pc ? 2 : 0}
          alignItems={pc ? "flex-start" : ""}
          justifyContent="center"
        >
          {props.info}
          {props.graph}
        </Stack>
      </Box>
    </Stack>
  );
}
