import {
  AppBar,
  Box,
  CssBaseline,
  Divider,
  Drawer,
  IconButton,
  Link,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Stack,
  Theme,
  Toolbar,
  Typography,
  useMediaQuery,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { Paths, updated } from "../../../constants";

function useHeader() {
  const navigate = useNavigate();
  const location = useLocation();
  const [open, setOpen] = React.useState(false);
  const disablePlayers = React.useMemo(
    () => location.pathname === Paths.Top,
    [location.pathname]
  );
  const disableAbout = React.useMemo(
    () => location.pathname === `/${Paths.About}`,
    [location.pathname]
  );
  const disableRelease = React.useMemo(
    () => location.pathname === "/" + Paths.Release,
    [location.pathname]
  );
  const onClickMenuButton = React.useCallback(() => setOpen(true), []);
  const onCloseMenu = React.useCallback(() => setOpen(false), []);
  const onClickPlayers = React.useCallback(
    () => navigate(Paths.Top),
    [navigate]
  );
  const onClickAbout = React.useCallback(
    () => navigate("/" + Paths.About),
    [navigate]
  );
  const onClickRelease = React.useCallback(
    () => navigate("/" + Paths.Release),
    [navigate]
  );

  return {
    open,
    onClickMenuButton,
    onCloseMenu,
    onClickPlayers,
    disablePlayers,
    disableAbout,
    onClickAbout,
    disableRelease,
    onClickRelease,
  };
}

export function Header(p: { drawerWidth: number }) {
  const pc = useMediaQuery<Theme>((theme) => theme.breakpoints.up("sm"));
  const props = useHeader();
  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: pc ? `calc(100% - ${p.drawerWidth}px)` : "100%",
          ml: pc ? `${p.drawerWidth}px` : undefined,
        }}
        elevation={0}
        color="primary"
      >
        <Toolbar sx={{ display: "flex", alignItems: "center" }}>
          {!pc && (
            <IconButton
              onClick={() => props.onClickMenuButton()}
              color="inherit"
              sx={{ marginRight: 1 }}
            >
              <MenuIcon />
            </IconButton>
          )}
          <Link underline="hover" href={Paths.Top} variant="h5" color="inherit">
            高田村インデックス
          </Link>
        </Toolbar>
      </AppBar>
      <Drawer
        sx={{
          width: p.drawerWidth,
          flexShrink: 0,
          "& .MuiDrawer-paper": {
            width: p.drawerWidth,
            boxSizing: "border-box",
          },
        }}
        variant={pc ? "permanent" : undefined}
        anchor="left"
        open={pc || props.open}
        onClose={() => props.onCloseMenu()}
      >
        <Toolbar>
          <Typography>{`最終更新 ${updated}`}</Typography>
        </Toolbar>
        <Divider />
        <List>
          <ListItem key={0} disablePadding>
            <ListItemButton
              onClick={() => props.onClickPlayers()}
              disabled={props.disablePlayers}
            >
              <ListItemText primary="プレイヤー一覧" />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
        <List>
          <ListItem key={0} disablePadding>
            <ListItemButton
              onClick={() => props.onClickAbout()}
              disabled={props.disableAbout}
            >
              <ListItemText primary="当サイトについて" />
            </ListItemButton>
          </ListItem>
          <ListItem key={1} disablePadding>
            <ListItemButton
              onClick={() => props.onClickRelease()}
              disabled={props.disableRelease}
            >
              <ListItemText primary="リリースノート" />
            </ListItemButton>
          </ListItem>
        </List>
        <Divider />
        <List>
          <ListItem key={0}>
            <Stack direction="row" spacing={1}>
              <Typography>© 2022</Typography>
              <Link href="https://twitter.com/testu0000" target="_blank">
                @tetsu0000
              </Link>
            </Stack>
          </ListItem>
        </List>
      </Drawer>
    </Box>
  );
}
