import { Avatar, Link, Theme, Typography, useMediaQuery } from "@mui/material";
import React from "react";
import { Table } from "../../molecules/Table";
import { imageSrc } from "../../../service/imageSrc";
import { Paths } from "../../../constants";

export type PlayerTableProps = {
  players: Array<string>;
  participationCount?: (player: string) => string;
  onClick: (path: string) => void;
};

export function PlayerTable(props: PlayerTableProps) {
  const pc = useMediaQuery<Theme>((theme) => theme.breakpoints.up("sm"));
  const playerRows = React.useMemo(
    () => rows(props.players, props.onClick, pc, props.participationCount),
    [pc, props.onClick, props.participationCount, props.players]
  );

  return (
    <Table
      rows={playerRows}
      sx={{
        height: "100%",
      }}
    />
  );
}

function rows(
  players: Array<string>,
  onClick: (path: string) => void,
  pc: boolean,
  participationCount?: (player: string) => string
): Array<Array<JSX.Element>> {
  return players.map(
    (player) =>
      [
        <Avatar
          alt={player}
          src={imageSrc(player)}
          sx={{
            cursor: "pointer",
          }}
          onClick={() => onClick(player)}
        />,
        <Link
          sx={{
            cursor: "pointer",
          }}
          color="inherit"
          href={`${Paths.Top}/${player}`}
        >
          {player}
        </Link>,
        participationCount ? (
          <Typography align="right" mr={pc ? 2 : 0}>
            {participationCount(player)}
          </Typography>
        ) : null,
      ].filter((element) => Boolean(element)) as JSX.Element[]
  );
}
