import React from "react";
import { useNavigate } from "react-router-dom";
import { Paths } from "../../../constants";
import {
  historys,
  participationMapping,
} from "../../../service/takadamuraHistoryService";
import { PlayerTableProps } from "./PlayerTable";

export function usePlayerTable(): PlayerTableProps {
  const players = React.useMemo(
    () => participationMapping.map((mapping) => mapping.player),
    []
  );
  const totalCount = React.useMemo(() => historys.length, []);
  const participationCount = React.useCallback(
    (player: string) => {
      const count =
        participationMapping.find((map) => map.player === player)?.count || 0;
      return `${count}/${totalCount}`;
    },
    [totalCount]
  );
  const navigate = useNavigate();
  const onClick = React.useCallback(
    (path: string) => navigate(`${Paths.Top}/${path}`),
    [navigate]
  );
  return {
    players,
    participationCount,
    onClick,
  };
}
