import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  IconButton,
  Typography,
} from "@mui/material";
import YouTubeIcon from "@mui/icons-material/YouTube";
import TwitterIcon from "@mui/icons-material/Twitter";

import { updated } from "../../../constants";

export type InfoCardProps = {
  alt: string;
  src: string;
  player: string;
  firstDate: string;
  lastDate: string;
  onClickYoutube?: () => void;
  onClickTwitter?: () => void;
};

export function InfoCard(props: InfoCardProps) {
  return (
    <Card>
      <CardMedia
        component="img"
        height={300}
        alt={props.alt}
        image={props.src}
      />
      <CardContent>
        <Typography variant="h5">{props.player}</Typography>
        <Typography mt={2}>{`初回参加日: ${props.firstDate}`}</Typography>
        <Typography>{`前回参加日: ${props.lastDate}`}</Typography>
        <Typography variant="body2">{`※集計期間: 2021/02/01~${updated}`}</Typography>
      </CardContent>
      <CardActions>
        <IconButton
          onClick={() => {
            if (!props.onClickYoutube) {
              return;
            }
            props.onClickYoutube();
          }}
          disabled={!props.onClickYoutube}
        >
          <YouTubeIcon fontSize="large" />
        </IconButton>
        <IconButton
          onClick={() => {
            if (!props.onClickTwitter) {
              return;
            }
            props.onClickTwitter();
          }}
          disabled={!props.onClickTwitter}
        >
          <TwitterIcon fontSize="large" />
        </IconButton>
      </CardActions>
    </Card>
  );
}
