import { BrowserRouter, Route, Routes } from "react-router-dom";
import { AboutAppPage } from "./components/pages/AboutAppPage";
import { InfoPage } from "./components/pages/InfoPage";
import { MainPage } from "./components/pages/MainPage";
import { ReleaseNotePage } from "./components/pages/ReleaseNotePage";
import { Paths } from "./constants";

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path={Paths.Top} element={<MainPage />} />
        <Route
          path={Paths.Top + "/" + Paths.PlayerInfo}
          element={<InfoPage />}
        />
        <Route path={Paths.About} element={<AboutAppPage />} />
        <Route path={Paths.Release} element={<ReleaseNotePage />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
