import {
  Card,
  CardContent,
  CardHeader,
  MenuItem,
  Select,
  SelectChangeEvent,
  Stack,
} from "@mui/material";
import { Chart as ChartJS, ChartData, registerables } from "chart.js";
import { Bar } from "react-chartjs-2";
ChartJS.register(...registerables);

export type ParticipationGraphProps = {
  data: ChartData<"bar", number[], unknown>;
  onChange: (event: SelectChangeEvent<unknown>) => void;
  years: Array<string>;
  year: string;
};

export function ParticipationGraph(props: ParticipationGraphProps) {
  return (
    <Card>
      <CardHeader title="月別参加回数" />
      <CardContent>
        <Stack>
          <Select
            onChange={(event) => props.onChange(event)}
            value={props.year}
            sx={{ width: "fit-content" }}
          >
            {props.years.map((year, i) => (
              <MenuItem value={year} key={i}>
                {year}
              </MenuItem>
            ))}
          </Select>
          <Bar data={props.data} width={400} />
        </Stack>
      </CardContent>
    </Card>
  );
}
