import { SelectChangeEvent } from "@mui/material";
import { blue } from "@mui/material/colors";
import React from "react";
import { useLocation } from "react-router-dom";
import { historys } from "../../../service/takadamuraHistoryService";
import { ParticipationGraphProps } from "./ParticipationGraph";

function getData(year: string, player: string) {
  const month = [...Array(12)];
  const _historys = historys.filter((h) =>
    Boolean(h.players.find((p) => p === player))
  );
  const datas = month.map((_, i) => {
    const count = _historys.filter((h) => {
      const list = h.timestamp.split("/");
      if (list[0] !== year) {
        return false;
      } else if (parseInt(list[1]) !== i + 1) {
        return false;
      }
      return true;
    }).length;
    return count;
  });
  return datas;
}

export function useParticipationGraph(): ParticipationGraphProps {
  const location = useLocation();
  const player = React.useMemo(
    () => decodeURIComponent(location.pathname.split("/")[2]),
    [location.pathname]
  );
  const labels = [...Array(12)].map((_, i) => `${i + 1}月`);
  const [year, setYear] = React.useState("2021");
  const years = ["2021", "2022"];
  const onChange = React.useCallback(
    (event: SelectChangeEvent<unknown>) =>
      setYear(event.target.value as string),
    []
  );
  const datas = React.useMemo(() => getData(year, player), [player, year]);
  return {
    data: {
      labels: labels,
      datasets: [
        {
          data: datas,
          label: "参加回数",
          backgroundColor: blue[600],
        },
      ],
    },
    onChange,
    year,
    years,
  };
}
