import {
  Paper,
  SxProps,
  Table as MuiTable,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  Theme,
} from "@mui/material";

export type TableProps = {
  rows: Array<Array<JSX.Element>>;
  sx?: SxProps<Theme>;
};

export function Table(props: TableProps) {
  return (
    <TableContainer component={Paper} sx={props.sx}>
      <MuiTable>
        <TableBody>
          {props.rows.map((row, i) => (
            <TableRow key={i}>
              {row.map((r, j) => (
                <TableCell key={j}>{r}</TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </MuiTable>
    </TableContainer>
  );
}
