import { Breadcrumb } from "../organisms/common/Breadcrumb";
import { InfoCard } from "../organisms/info/InfoCard";
import { ParticipationGraph } from "../organisms/info/ParticipationGraph";
import { useInfoBreadCrumb } from "../organisms/info/useInfoBreadCrumb";
import { useInfoCard } from "../organisms/info/useInfoCard";
import { useParticipationGraph } from "../organisms/info/useParticipationGraph";
import { InfoTemplate } from "../templates/InfoTemplate";

export function InfoPage() {
  const breadcrumbProps = useInfoBreadCrumb();
  const infoCardProps = useInfoCard();
  const graphProps = useParticipationGraph();
  return (
    <InfoTemplate
      breadcrumb={<Breadcrumb {...breadcrumbProps} />}
      info={<InfoCard {...infoCardProps} />}
      graph={<ParticipationGraph {...graphProps} />}
    />
  );
}
